import { isDisabled } from "@testing-library/user-event/dist/utils";
import { useState } from "react";
import "./App.css";

function Modal(props) {
  return (
    <div className="modal">
      <TheDate />
      <p>상세내용</p>
    </div>
  );
}

function TheDate() {
  let now = new Date();
  let todayYear = now.getFullYear();
  let todayMonth = now.getMonth() + 1;
  let todayDate = now.getDate();
  let todayHours = now.getHours();
  let todayMinutes = now.getMinutes();
  return (
    <div>
      <h4>
        날짜 : {todayYear}.{todayMonth}.{todayDate} / {todayHours}시
        {todayMinutes}분
      </h4>
    </div>
  );
}

function App() {
  const [title, setTitle] = useState([
    "남자 코트 추천",
    "강남 우동 맛집",
    "파이썬 독학",
  ]);
  const [value, setValue] = useState("");
  const [titleNum, setTitleNum] = useState(0);
  const day = "2월 17일 발행";
  const [like, setLike] = useState([0, 0, 0]);
  const [modal, setModal] = useState(false);

  return (
    <div className="all">
      <div className="welcome">
        <h4>React Blog</h4>
      </div>
      {title.map((items, index) => {
        return (
          <div
            className="list"
            onClick={() => {
              if (modal === false) {
                setModal(true);
              } else {
                setModal(false);
              }
            }}
          >
            <h4
              onClick={() => {
                setTitleNum(index);
              }}
            >
              {items}
            </h4>
            <span
              onClick={() => {
                const newLike = [...like];
                newLike[index] = newLike[index] + 1;
                setLike(newLike);
              }}
            >
              {" "}
              👍{" "}
            </span>

            {like[index]}
            <p>{day}</p>
            <button
              style={{ marginBottom: "10px" }}
              onClick={(e) => {
                const newTitle = [...title];
                newTitle.splice(index, 1);
                setTitle(newTitle);
              }}
            >
              삭제하기
            </button>
          </div>
        );
      })}
      <input
        type="text"
        onChange={(e) => {
          setValue(e.target.value);
        }}
      ></input>
      <button
        style={{ marginTop: 10 }}
        onClick={(e) => {
          if (value === "") {
            e.isDisabled();
          }
          const newTitle = [...title];
          const newLike = [...like];
          newTitle.push(value);
          setTitle(newTitle);
          newLike.push(0);
          setLike(newLike);
        }}
      >
        글 작성
      </button>

      {modal === true ? <Modal title={title} num={titleNum} /> : null}
    </div>
  );
}

export default App;
